@import url(https://fonts.googleapis.com/css?family=Roboto:500);
@import url('https://fonts.googleapis.com/css2?family=Honk&family=Lilita+One&display=swap');
.App {
  text-align: center;
  padding: 2% 5%;
}

body{
  background-image: linear-gradient(to right, black, white, black);
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

button{
  transition: all .5s;
}

button:hover {
  transform: scale(1.5);
}

button:active {
  transform: scale(1);
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-bottom: 20px;
  background-color: #02142b;
  color: white;
}

.city {
  width: 250px;
}

#city-search {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #000000;
  color: white;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
  color: #02142b;
  cursor: pointer;
}

.event-element {
  padding: 10px 50px;
  margin-bottom: 10px;
  text-align: center;
  border: none;
  position: relative;
  font-size: 125%;
  transition: all .5s ease;
}

.event-title {
  font-family: 'Honk', system-ui;
  font-size: 200%;
}

.event-location{
  font-weight: bold;
}

.event-date{
  font-weight: bolder;
}

.expanded {
  background-image: linear-gradient( #02142b, white, white, rgb(209, 205, 205), #02142b);
  color: #02142b;
  border-radius: 25% 50%;
  padding: 10px 75px;
}

.collapsed {
  background-image: linear-gradient(to right, #02142b, #02142b, #02142b, #02142b, #02142b);
  color: white;
  border-radius: 25% 50%;
}




.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  scale: 1.25;
}